import { axiosInstance } from "@/lib/axios";
import { Poll } from "@/types/poll.types";
import toast from "react-hot-toast";
import { create } from "zustand";

export const pollStore = create((set) => ({
  poll: null,
  isCreatingPoll: false,
  isFetchingPolls: false,

  CreatePoll: async (data: Poll) => {
    set({ isCreatingPoll: true });
    try {
      const res = await axiosInstance.post("/poll/me/create", data);
      set({ poll: res.data });
      toast.success("Poll Created Successfully");
    } catch (error: unknown) {
      console.log(error);
      let errorMessage = "An unknown error occurred";
      if (typeof error === "object" && error !== null && "response" in error) {
        const err = error as {
          response?: { data?: { error?: string } };
          message?: string;
        };
        errorMessage = err.response?.data?.error || err.message || errorMessage;
      } else if (typeof error === "string") {
        errorMessage = error;
      }
      toast.error("Error Creating Poll: " + errorMessage);
      return false;
    } finally {
      set({ isCreatingPoll: false });
      return true;
    }
  },
}));
