import React from "react";
import { Box, TextField, Button, Typography } from "@mui/material";
// import { useRouter } from "next/navigation";
import { userStore } from "@/store/userStore";
import { UserStoreType } from "@/types/user.types";
import { pollStore } from "@/store/pollStore";
import Link from "next/link";
import { PollStoreType } from "@/types/poll.types";

function createPage() {
  const { user } = userStore() as UserStoreType;
  const { CreatePoll, isCreatingPoll } = pollStore() as PollStoreType;
  const isLoggedIn = user;
  let created: boolean = false;

  const handleSubmit = async (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    const form = e.currentTarget;
    const question = form.question.value;
    const options: string[] = form.options.value;

    if (!question || options.length === 0 || options.some((opt) => !opt)) {
      alert("Please fill in all fields correctly.");
      return;
    }

    const data = {
      question,
      options,
    };
    created = CreatePoll(data);
  };
  // const handleCopy = () => {
  //   if (pollId) {
  //     navigator.clipboard.writeText(`/vote/${pollId}`);
  //     alert("Poll link copied to clipboard!");
  //   }
  // };

  return (
    <div className="flex flex-col items-center p-8 min-h-screen">
      <Typography variant="h4" className="mb-6 font-bold text-white">
        Create Poll
      </Typography>

      {isLoggedIn ? (
        !created ? (
          <Box
            component="form"
            onSubmit={handleSubmit}
            noValidate
            className="w-full max-w-md space-y-6"
          >
            <TextField
              label="Poll Question"
              required
              fullWidth
              InputLabelProps={{ style: { color: "#ccc" } }}
              InputProps={{ style: { color: "#fff" } }}
            />

            <TextField
              label="Options (comma separated)"
              required
              fullWidth
              InputLabelProps={{ style: { color: "#ccc" } }}
              InputProps={{ style: { color: "#fff" } }}
            />

            <Button
              type="submit"
              variant="contained"
              color="primary"
              fullWidth
              disabled={isCreatingPoll}
            >
              {isCreatingPoll ? "Creating..." : "Create Poll"}
            </Button>
          </Box>
        ) : (
          <div className="flex flex-col items-center space-y-4 mt-8">
            <Typography variant="h6" className="text-green-400">
              🎉 Poll created successfully!
            </Typography>

            {/* <Typography className="text-white">
              Poll ID: <span className="text-blue-400">{pollId}</span>
            </Typography>

            <Typography className="text-white">
              Share this link: <br />
              <a
                href={`http://localhost:3000/vote/${pollId}`}
                target="_blank"
                rel="noopener noreferrer"
                className="text-blue-400 underline"
              >
                http://localhost:3000/vote/{pollId}
              </a>
            </Typography>

            <Button onClick={handleCopy} variant="outlined" color="secondary">
              Copy Link
            </Button>

            <Button
              onClick={() => router.push("/")}
              variant="contained"
              color="primary"
            >
              Back to Home
            </Button> */}
          </div>
        )
      ) : (
        <div className="flex flex-col items-center space-y-4">
          <Typography variant="h5" className="text-white">
            To create a poll you need to log in
          </Typography>
          <div className="flex space-x-4">
            <Link href="/user/signup">
              <Button variant="outlined" color="primary">
                Sign Up
              </Button>
            </Link>
            <Link href="/user/login">
              <Button variant="contained" color="primary">
                Log In
              </Button>
            </Link>
          </div>
        </div>
      )}
    </div>
  );
}

export default createPage;
